const {Component, Mixin} = Shopware;

import template from './heidipay-settings.html.twig';
import './style.scss';

Component.register('heidipay-settings', {
    template,

    mixins: [
        Mixin.getByName('notification'),
        Mixin.getByName('sw-inline-snippet')
    ],

    inject: [ 'HeidiPaySettingsService' ],

    data() {
        return {
            config: {},
            isLoading: false,
            isTesting: false,
            isSaveSuccessful: false,
            isTestSuccessful: false,
            HeidiSecretKey: false,
            isSupportModalOpen: false,
        }
    },
    computed: {
        credentialsMissing: function () {
            return !this.HeidiSecretKey;
        },
        requiredMissing: function () {
            return !this.HeidiSecretKey;
        }
    },
    methods: {
        saveFinish() {
            this.isSaveSuccessful = false;
        },
        onSave() {
            this.isLoading = true;

            if (this.credentialsMissing) {
                this.createNotificationError({
                    title: this.$tc('HeidiPay.settingsForm.messages.messageTestTitleEmpty'),
                    message: this.$tc('HeidiPay.settingsForm.messages.messageTestErrorEmpty')
                });
                this.isLoading = false;
                return;
            }
            if (this.requiredMissing) {
                this.createNotificationError({
                    title: this.$tc('HeidiPay.settingsForm.messages.titleMissing'),
                    message: this.$tc('HeidiPay.settingsForm.messages.messageMissing')
                });
                this.isLoading = false;
                return;
            }

            this.isSaveSuccessful = false;
            this.$refs.systemConfig.saveAll().then(() => {
                this.isLoading = false;
                this.isSaveSuccessful = true;
            }).catch(() => {
                this.isLoading = false;
            });
        },

        onTest() {
            this.isTesting = true;
            this.isTestSuccessful = false;

            let credentials = this.getConfigValue('heidiSecretKey');

            this.HeidiPaySettingsService.validateApiCredentials(credentials).then((response) => {
                const credentialsValid = response.credentialsValid;
                const error = response.error;

                if (credentialsValid) {
                    this.createNotificationSuccess({
                        title: this.$tc('HeidiPay.settingsForm.messages.titleSuccess'),
                        message: this.$tc('HeidiPay.settingsForm.messages.messageTestSuccess')
                    });
                    this.isTestSuccessful = true;
                } else {
                    this.createNotificationError({
                        title: this.$tc('HeidiPay.settingsForm.messages.titleError'),
                        message: this.$tc('HeidiPay.settingsForm.messages.messageTestError')
                    });
                }
                this.isTesting = false;
            }).catch((errorResponse) => {
                this.createNotificationError({
                    title: this.$tc('HeidiPay.settingsForm.messages.titleError'),
                    message: this.$tc('HeidiPay.settingsForm.messages.messageTestErrorGeneral')
                });
                this.isTesting = false;
            });
        },

        onConfigChange(config) {
            this.config = config;
            this.checkCredentialsFilled();
        },

        getBind(element, config) {
            console.log('Function getBind');
            console.log(config);
            console.log(this.config);
            if (config !== this.config) {
                this.onConfigChange(config);
            }

            return element;
        },
        checkCredentialsFilled() {
            this.HeidiSecretKey = !!this.getConfigValue('heidiSecretKey');
        },
        getConfigValue(field) {
            const defaultConfig = this.$refs.systemConfig.actualConfigData.null;
            const salesChannelId = this.$refs.systemConfig.currentSalesChannelId;

            if (salesChannelId === null) {
                return this.config[`Go2FlowHeidiPayPayment.settings.${field}`];
            }
            return this.config[`Go2FlowHeidiPayPayment.settings.${field}`]
                || defaultConfig[`Go2FlowHeidiPayPayment.settings.${field}`];
        },
    }
});
